import { mapState,mapGetters } from 'vuex';
import stepOne from './step1.vue';
import stepTwo from './step2.vue';
import stepThree from './step3.vue';
export default {
	data() {
		return {
			numList: [
				{
					name: '基本信息'
				},
				{
					name: '工作信息完善'
				},
				{
					name: '完成'
				}
			],
			num: 0,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			show_tips:false,
			tip_text:''
		};
	},
	onLoad() {
		this.init()
	},
	components: {
		stepOne,
		stepTwo,
		stepThree
	},
	computed: {
		...mapState(['user_info']),
		...mapGetters(['id']),
	},
	methods: {
		init(){
			if(this.id){
				this.num = 1
				this.showTips('更完整的工作信息将会给您带来更多的工作机会')
			}
		},
		scroll(e) {
			console.log(e);
			this.old.scrollTop = e.detail.scrollTop;
		},
		next(){
			this.num++
			if(this.num === 2){
				this.show_tips = false
			}
		},
		showTips(data){
			this.show_tips = true
			this.tip_text = data
		}
	}
};
